@import url("https://fonts.googleapis.com/css2?family=Grand+Hotel&display=swap");
.navBg a {
  color: white !important;
}

.brand-logo {
  font-family: "Grand Hotel", cursive;
}

.mycard {
  margin-top: 30px;
}

.mycard h2 {
  font-family: "Grand Hotel", cursive;
}

.card__auth {
  padding: 20px;
  text-align: center;
  max-width: 400px;
  margin: 0 auto;
}

p.text-left {
  margin-top: 3px;
  text-align: left;
}

.profile {
  padding: 0 100px;
}

.profile_img {
  width: 160px;
  height: 160px;
  border-radius: 50%;
  margin-right: 100px;
  object-fit: cover;
}

.profile_img.image {
  margin: 0 auto;
}

.profile_img.image + .middles {
  width: 160px;
  height: 160px;
}

.profile_img.image + .middles .texts {
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
}

.profile_header {
  display: flex;
  align-items: center;
  justify-content: space-around;
}

.name-field {
  display: flex;
  align-items: center;
}

.name-field > div:nth-child(2) {
  margin-left: 10px;
  width: 100%;
}

.profile_main {
  display: flex;
  justify-content: center;
  margin: 20px auto;
  margin-bottom: 10px;
  border-bottom: 2px solid rgb(163, 162, 162);
  padding: 0 0 20px 0;
}

.profile_main > div {
  width: 45%;
  text-align: center;
}

.intro_profile {
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: space-around;
}

.gallery {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(250px, 1fr));
  gap: 1rem;
}

.img_item {
  display: flex;
  flex-direction: column;
}

.img-item img {
  width: 100%;
}

.home_card {
  max-width: 500px;
  height: max-content;
  margin: 30px auto;
  padding: 10px;
}

.postCard {
  margin: 10px auto;
  padding: 20px;
  max-width: 500px;
  text-align: center;
}

.postCard h2 {
  font-family: "Grand Hotel", cursive;
}

.postBody {
  background-color: #263238;
  padding: 30px 0 0 0;
  height: 90vh;
}

.cardPost {
  position: relative;
  margin: 0 auto;
  width: 800px;
  height: 530px;
  background: #fff;
  box-shadow: 0 15px 50px rgba(0, 0, 0, 0.1);
}

.cardPost img {
  width: 800px;
  height: 300px;
  object-fit: cover;
}

.file_wrapper {
  position: relative;
}

.file_icon {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
}

.file_img {
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  z-index: 100;
  opacity: 0;
}
